.body{
    height: 100vh;
    margin-top: 15vh;
}
.content-section {
    justify-content:center;
    align-items: center;
    padding: 20px;
  }
  
  .brand {
    display: flex;
    align-items: center;
    margin:20px;
  }
  
  .brand-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:
  }
  
  .brand-name {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    margin: 0;
  }
  
  .search-bar {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .search{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .search-input {
    display: flex;
    align-items: center;
    height: 7vh;
    width: 60%; /* Adjust as per your requirement */
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .search-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 2vh;
  }
  
  .search-button:hover {
    background-color: #0056b3;
  }
  